import { cloneDeep } from 'lodash';
import ih from 'immutability-helper';

import * as types from './actionTypes';
import initialState from './initialState';

function clearUiComponents(thread) {
  return thread.filter(
    item =>
      item.type !== 'ui_component' ||
      (item.type === 'ui_component' && item.keepVisible),
  );
}

interface actionInterface {
  type?: string;
  payload?: {
    id?: string;
    message?: object;
    messages?: object[];
    error?: string;
    isOpen?: boolean;
    isMinimized?: boolean;
  };
}

export default (
  state = cloneDeep(initialState),
  action: actionInterface = {},
) => {
  switch (action.type) {
    case types.START_CHAT_REQUEST:
      return ih(state, {
        data: {
          thread: {
            $apply: thread =>
              thread.length > 0
                ? thread
                    .filter(item => item.type !== 'preloader')
                    .concat([
                      {
                        type: 'divider',
                      },
                    ])
                : [],
          },
        },
        start: {
          $merge: {
            isLoading: true,
            error: '',
          },
        },
      });
    case types.START_CHAT_SUCCESS:
      return ih(state, {
        data: {
          id: { $set: action.payload.id },
        },
        start: {
          $merge: {
            isLoaded: true,
            isLoading: false,
          },
        },
      });
    case types.START_CHAT_ERROR:
      return ih(state, {
        start: {
          $merge: {
            isLoading: false,
            error: action.payload.error,
          },
        },
      });
    case types.SAVE_USER_RESPONSE:
      return ih(state, {
        data: {
          thread: {
            $apply: thread =>
              clearUiComponents(thread).concat([action.payload.message]),
          },
        },
      });
    case types.SAVE_BOT_RESPONSE:
      return ih(state, {
        data: {
          thread: {
            $apply: thread =>
              thread
                .filter(item => item.type !== 'preloader')
                .concat(action.payload.messages)
                .map(item => {
                  const keepVisible =
                    item.type === 'ui_component' &&
                    ['CALENDAR', 'FORM', 'MIXED'].includes(item.message.type);

                  return { ...item, keepVisible };
                }),
          },
        },
      });
    case types.SAVE_BOT_TYPING:
      return ih(state, {
        data: {
          thread: {
            $apply: thread =>
              thread
                .filter(item => item.type !== 'preloader')
                .concat([
                  {
                    type: 'preloader',
                  },
                ]),
          },
        },
      });
    case types.SEND_TEXT_MESSAGE_REQUEST:
      return ih(state, {
        ask: {
          $merge: {
            isLoading: true,
            error: '',
          },
        },
      });
    case types.SEND_TEXT_MESSAGE_SUCCESS:
      return ih(state, {
        ask: {
          isLoading: { $set: false },
        },
      });
    case types.SEND_TEXT_MESSAGE_ERROR:
      return ih(state, {
        ask: {
          $merge: {
            isLoading: false,
            error: action.payload.error,
          },
        },
        data: {
          thread: {
            $apply: thread => thread.filter(item => item.type !== 'preloader'),
          },
        },
      });
    case types.STOP_CHAT:
      return ih(state, {
        ask: {
          isLoading: { $set: false },
        },
      });
    case types.OPEN_CHAT_BOX:
      return ih(state, {
        $merge: {
          isOpen: true,
          isMinimized: false,
        },
      });
    case types.TOGGLE_MINIMIZE_CHAT_BOX:
      return ih(state, {
        $toggle: ['isMinimized'],
      });
    case types.CLOSE_CHAT_BOX:
      return ih(state, {
        $set: initialState,
      });
    case types.RESTART_CHAT:
      return ih(state, {
        isOpen: { $set: true },
        isMinimized: { $set: false },
        data: {
          thread: { $set: [] },
        },
      });
    case types.UPDATE_DIALOG_ID: {
      return ih(state, {
        data: {
          id: { $set: action.payload.id },
        },
      });
    }
    case types.SET_DIALOG_STATUS:
      return ih(state, {
        data: {
          thread: {
            $apply: thread => thread.concat([action.payload.message]),
          },
        },
      });
    default:
      return state;
  }
};
