import { applyMiddleware, createStore, combineReducers, compose } from 'redux';
import thunk from 'redux-thunk';
import ReconnectingWebSocket from 'reconnecting-websocket';

import sessionStore from 'store/utils/sessionStorage';
import wsStatuses from 'constants/wsStatuses';
import {
  saveBotPromptsResponse,
  saveBotTyping,
  startChatSuccess,
  setDialogStatus,
  updateDialogId,
} from 'store/services/chat/actions';
import reducers from './services';
import { setError } from './services/errors';

function initializeWithWs(apiUrl, initialState) {
  const rootReducer = combineReducers(reducers);

  const ws = new ReconnectingWebSocket(apiUrl, [], { debug: true });

  const isDevelopment = process.env.NODE_ENV === 'development';
  // @ts-ignore
  const browserExtensionCompose = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__;

  const composeEnhancers =
    isDevelopment && browserExtensionCompose
      ? browserExtensionCompose
      : compose;

  const store = createStore(
    rootReducer,
    initialState,
    composeEnhancers(applyMiddleware(thunk.withExtraArgument(ws))),
  );

  ws.onopen = event => {
    console.log(event, 'ws is open');
  };

  ws.onmessage = event => {
    const data = JSON.parse(event.data);
    // TODO: We need to reconsider event with type="7"
    // TODO: which is the only sent without payload
    const { type, payload = {} } = data;
    const { dialog_id, responses } = payload;

    if (type === wsStatuses['TYPE_DIALOG_START_RESPONSE']) {
      store.dispatch(startChatSuccess(dialog_id));
      store.dispatch(saveBotPromptsResponse(responses));
    }

    if (type === wsStatuses['TYPE_DIALOG_TYPING']) {
      store.dispatch(saveBotTyping());
    }

    if (type === wsStatuses['TYPE_DIALOG_RESPONSE']) {
      // The dialog id is always updated in case a session is restarted. In this
      // case the messages sent by the API will contain a new id that the chat bot
      // must now use.
      store.dispatch(updateDialogId(dialog_id));

      if (responses.length) {
        store.dispatch(saveBotPromptsResponse(responses));
      }
    }

    if (type === wsStatuses.TYPE_ERROR) {
      store.dispatch(setError(payload));
    }

    if (type === wsStatuses['TYPE_DIALOG_STATUS']) {
      store.dispatch(setDialogStatus(payload.message));
    }
  };

  ws.onerror = event => {
    console.log(event, 'ws has error');
  };

  ws.onclose = event => {
    console.log(event, 'ws is closed');
  };

  return store;
}

function initializeWithoutWS(endpointUrl, initialState) {
  const rootReducer = combineReducers(reducers);

  const isDevelopment = process.env.NODE_ENV === 'development';
  // @ts-ignore
  const browserExtensionCompose = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__;

  const composeEnhancers =
    isDevelopment && browserExtensionCompose
      ? browserExtensionCompose
      : compose;

  const store = createStore(
    rootReducer,
    initialState,
    composeEnhancers(applyMiddleware(thunk)),
  );

  return store;
}

function initializeStore({
  apiUrl,
  enableLocalStorage,
  enableWS,
  initialState = {},
}: {
  apiUrl: string;
  enableLocalStorage: boolean;
  enableWS: boolean;
  initialState?: object;
}) {
  const initialStoreState = enableLocalStorage
    ? sessionStore.loadState()
    : initialState;

  const store = enableWS
    ? initializeWithWs(apiUrl, initialStoreState)
    : initializeWithoutWS(apiUrl, initialStoreState);

  if (enableLocalStorage) {
    store.subscribe(() => {
      const { chat, session } = store.getState();

      sessionStore.saveState({
        chat,
        session,
      });
    });
  }

  return store;
}

export default initializeStore;
