import { createGlobalStyle } from 'styled-components';
import { get, curry } from 'lodash';
import { colors } from '@omilia/chat-ui';

import logo from './encodedImage';

createGlobalStyle`
body {
  @import url('https://fonts.googleapis.com/css?family=Montserrat&display=swap');
}
`;

export interface ITheme {
  primaryColor?: string;
  logoUrl?: string;
  fontFamily?: string;
  title?: string;
}

const baseTheme: ITheme = {
  primaryColor: colors.COLOR_BRAND,
  logoUrl: logo,
  fontFamily: `Montserrat, sans-serif`,
  title: 'Omilia Chat',
};

export default baseTheme;

/**
 * Shallowly mix a theme with the base one.
 */
export const makeTheme: (theme: ITheme) => ITheme = theme => ({
  ...baseTheme,
  ...theme,
});

/**
 * Extract theme props with confidence
 */
export const getThemeProp = curry((key: string, props: any) =>
  get(props, `theme.${key}`, get(baseTheme, key)),
);
