import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { ThemeProvider } from 'styled-components';

import { makeTheme } from './styles/theme';
import { setToken } from './store/services/session/actions';
import initializeStore from './store';
import Chat from './containers/Chat';
import {
  openChatBox,
  saveBotPromptsResponse,
  startChatSuccess,
} from './store/services/chat/actions';

import quickRepliesMessage from './mocks/quickRepliesMessage';

interface Config {
  title?: string;
  logoUrl?: string;
  brandColor?: string;
  apiUrl: string;
  apiKey: string;
  enableLocalStorage?: boolean;
  enableWS?: boolean;
  defaultSize?: object;
}

interface Configuration {
  rootElementId: string;
  config: Config;
}

export function ChatApp({
  title,
  logoUrl,
  brandColor,
  apiUrl,
  apiKey,
  enableLocalStorage = true,
  enableWS = true,
}: Config) {
  const theme = makeTheme({ logoUrl, primaryColor: brandColor, title });
  const store = initializeStore({ apiUrl, enableLocalStorage, enableWS });
  store.dispatch(setToken(apiKey));

  return (
    <ThemeProvider theme={theme}>
      <Provider store={store}>
        <Chat />
      </Provider>
    </ThemeProvider>
  );
}

// The purpose of this component is just to be used in the-mechanic project. As
// soon as we find a better solution in the future this is going to be deleted.
export function DemoChatApp({
  title,
  logoUrl,
  brandColor,
  apiUrl,
  apiKey,
  enableLocalStorage = true,
  enableWS = true,
  defaultSize,
}: Config) {
  const theme = makeTheme({ logoUrl, primaryColor: brandColor, title });
  const store = initializeStore({ apiUrl, enableLocalStorage, enableWS });
  store.dispatch(setToken(apiKey));
  store.dispatch(openChatBox());
  store.dispatch(startChatSuccess(quickRepliesMessage.payload.dialog_id));
  store.dispatch(saveBotPromptsResponse(quickRepliesMessage.payload.responses));

  return (
    <ThemeProvider theme={theme}>
      <Provider store={store}>
        <Chat defaultSize={defaultSize} />
      </Provider>
    </ThemeProvider>
  );
}

export function init({ rootElementId, config }: Configuration) {
  return ReactDOM.render(
    <ChatApp {...config} />,
    document.getElementById(rootElementId),
  );
}

export default init;
