const KEY = 'chatbot';

export default {
  loadState() {
    try {
      const serializedState = sessionStorage.getItem(KEY);

      if (serializedState === null) {
        return undefined;
      }

      return JSON.parse(serializedState);
    } catch (err) {
      return undefined;
    }
  },

  saveState(state: object) {
    sessionStorage.setItem(KEY, JSON.stringify(state));
  },
};
