import * as React from 'react';
import styled from 'styled-components';
import { colors } from '@omilia/chat-ui';

import Button from '../Button';

const SErrorContainer = styled.div`
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
`;

const SIcon = styled.i`
  display: inline-block;
  height: auto;
  margin-bottom: 16px;
  width: 68px;
`;

const STitle = styled.div`
  color: ${colors.COLOR_GREY_600};
  font-size: 15px;
  line-height: 20px;
  margin-bottom: 47px;
`;

const SButton = styled(Button)`
  font-size: 15px;
  font-weight: 500;
  line-height: 23px;
  padding: 7px 22px;
`;

interface Prop {
  restartChat: () => void;
}

const Error = ({ restartChat }: Prop) => (
  <SErrorContainer>
    <SIcon
      dangerouslySetInnerHTML={{
        __html: require('!svg-inline-loader!icons/error.svg'),
      }}
    />
    <STitle>Oops! Something went wrong.</STitle>
    <SButton label="Restart Dialog" onClick={restartChat} />
  </SErrorContainer>
);

export default Error;
