import { createSelector } from 'reselect';

export const getChat = ({ chat }) => chat.data;

export const getThread = ({ chat }) => chat.data.thread;

export const getHasThread = createSelector(
  getThread,
  thread => thread.length > 0,
);

export const getIsStarting = ({ chat }) => chat.start.isLoading;

export const getIsLoaded = ({ chat }) => chat.start.isLoaded;

export const getIsReStarting = createSelector(
  getIsLoaded,
  getIsStarting,
  (isLoaded, isStarting) => isLoaded && isStarting,
);

export const getIsAsking = ({ chat }) => chat.ask.isLoading;

export const getIsOpen = ({ chat }) => chat.isOpen;

export const getIsMinimized = ({ chat }) => chat.isMinimized;

export const getHasError = ({ chat }) => chat.start.error !== '';

export const getError = ({ chat }) => chat.start.error;
