import React from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { Resizable } from 're-resizable';
import styled, { css } from 'styled-components';

import * as selectors from 'store/services/chat/selectors';
import * as actions from 'store/services/chat/actions';
import { hasError } from 'store/services/errors';

import ChatHeader from './ChatHeader';
import ChatContent from './ChatContent';
import ChatFooter from './ChatFooter';

import { getThemeProp } from 'styles/theme';

const getChatStyle = css`
  border-radius: 8px;
  bottom: 30px;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.15);
  display: flex;
  flex-direction: column;
  font: 14px ${getThemeProp('fontFamily')};
  max-height: calc(100vh - 30px);
  overflow: visible;
  right: 30px;
  width: 376px;
  z-index: 999;
`;

const SResizableContainer = styled(Resizable)`
  ${getChatStyle}
`;

const DEFAULT_SIZE = {
  width: 376,
  height: 500,
};

const ChatWidget = props => (
  <SResizableContainer
    defaultSize={...{ ...DEFAULT_SIZE, ...props.defaultSize }}
    style={{ position: 'fixed' }}
    minWidth={300}
    maxWidth={400}
    minHeight={400}
  >
    <ChatHeader
      toggleMinimizeChatBox={props.toggleMinimizeChatBox}
      closeChatBox={props.closeChatBox}
    />
    <ChatContent
      isLoaded={props.isLoaded}
      isStarting={props.isStarting}
      isAsking={props.isAsking}
      hasError={props.hasError}
      thread={props.thread}
      sendMessage={props.sendMessage}
      restartChat={props.restartChat}
    />
    <ChatFooter
      isOpen={props.isOpen}
      isLoaded={props.isLoaded}
      isStarting={props.isStarting}
      isAsking={props.isAsking}
      hasError={props.hasError}
      sendMessage={props.sendMessage}
    />
  </SResizableContainer>
);

const MinimizedWrapper = styled.div`
  ${getChatStyle};

  bottom: 0;
  position: fixed;
  right: 20px;
`;

const MinimizedChatWidget = props => {
  return (
    <MinimizedWrapper>
      <ChatHeader
        toggleMinimizeChatBox={props.toggleMinimizeChatBox}
        closeChatBox={props.closeChatBox}
      />
    </MinimizedWrapper>
  );
};

const BubbleIcon = styled.i`
  bottom: 30px;
  display: inline-block;
  height: 60px;
  position: fixed;
  right: 30px;
  width: 60px;

  svg circle {
    fill: ${getThemeProp('primaryColor')};
  }

  &:hover {
    cursor: pointer;
  }
`;

const BubbleWidget = props => {
  const handleOpenChatBox = () => {
    props.openChatBox();
    props.startChat();
  };

  return (
    <BubbleIcon
      dangerouslySetInnerHTML={{
        __html: require('!svg-inline-loader!icons/chatbot-icon.svg'),
      }}
      data-test="icon-chat"
      onClick={handleOpenChatBox}
    />
  );
};

export function Chat(props) {
  const { isOpen, isMinimized } = props;
  let Component;

  if (isOpen) {
    Component = isMinimized ? MinimizedChatWidget : ChatWidget;
  } else {
    Component = BubbleWidget;
  }

  return <Component {...props} />;
}

const mapStateToProps = state => ({
  isOpen: selectors.getIsOpen(state),
  isMinimized: selectors.getIsMinimized(state),
  isLoaded: selectors.getIsLoaded(state),
  isStarting: selectors.getIsStarting(state),
  isAsking: selectors.getIsAsking(state),
  hasError: selectors.getHasError(state) || hasError(state),
  thread: selectors.getThread(state),
  hasThread: selectors.getHasThread(state),
});

const mapDispatchToProps = dispatch => ({
  startChat: bindActionCreators(actions.startChat, dispatch),
  sendMessage: bindActionCreators(actions.sendTextMessage, dispatch),
  openChatBox: bindActionCreators(actions.openChatBox, dispatch),
  toggleMinimizeChatBox: bindActionCreators(
    actions.toggleMinimizeChatBox,
    dispatch,
  ),
  closeChatBox: bindActionCreators(actions.closeChatBox, dispatch),
  restartChat: bindActionCreators(actions.restartChat, dispatch),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(Chat);
