import React from 'react';
import styled, { ThemeContext } from 'styled-components';
import { colors } from '@omilia/chat-ui';

import IconClose from 'components/Icons/IconClose';
import IconMinimize from 'components/Icons/IconMinimize';
import Logo from 'components/Logo';

import { getThemeProp } from 'styles/theme';

interface Props {
  toggleMinimizeChatBox: () => void;
  closeChatBox: () => void;
}

const SHeader = styled.div`
  align-items: center;
  background-color: ${getThemeProp('primaryColor')};
  border-radius: 8px 8px 0 0;
  box-shadow: 0 9px 18px 0 rgba(0, 0, 0, 0.08), 0 2px 4px 0 rgba(0, 0, 0, 0.12);
  display: flex;
  height: 56px;
  justify-content: space-between;
  padding: 0 16.6px;
  z-index: 1;
`;

const SLogo = styled(Logo)`
  border: 1.4px solid ${colors.COLOR_WHITE};
  height: 38.8px;
  margin-right: 12.6px;
  width: 38.8px;
`;

const STitle = styled.span`
  align-items: center;
  color: ${colors.COLOR_WHITE};
  display: inline-flex;
  font-size: 16px;
  font-weight: 600;
  justify-content: center;
  line-height: 25px;
  font-family: ${getThemeProp('fontFamily')};
`;

const SControls = styled.div`
  align-items: center;
  display: inline-flex;
  justify-content: flex-end;
`;

const SControl = styled.a`
  box-sizing: content-box;
  color: ${colors.COLOR_WHITE};
  cursor: pointer;
  display: inline-block;
  height: 15px;
  margin-left: 10px;
  text-align: center;
  width: 15px;
`;

const ChatHeader = ({ toggleMinimizeChatBox, closeChatBox }: Props) => {
  const theme = React.useContext(ThemeContext);

  const logoUrl = getThemeProp('logoUrl', { theme });
  const title = getThemeProp('title', { theme });

  return (
    <SHeader data-test="chat-header">
      <STitle>
        <SLogo src={logoUrl} alt={title} />
        {title}
      </STitle>

      <SControls>
        <SControl onClick={toggleMinimizeChatBox} data-test="icon-minimize">
          <IconMinimize
            data-test="button-minimize-chat"
            color={colors.COLOR_WHITE}
          />
        </SControl>

        <SControl onClick={closeChatBox} data-test="icon-close">
          <IconClose data-test="button-close-chat" color={colors.COLOR_WHITE} />
        </SControl>
      </SControls>
    </SHeader>
  );
};

export default ChatHeader;
