import moment from 'moment';
import { flatMap } from 'lodash';
import sleep from 'store/utils/sleep';
import wsStatuses from 'constants/wsStatuses';
import { getToken } from 'store/services/session/selectors';
import * as types from './actionTypes';
import { getChat } from './selectors';

/**
 * Save Bot's messages
 *
 * @param responses
 * @returns {{type: string, payload: {responses: *}}}
 */
export const saveBotPromptsResponse = responses => ({
  type: types.SAVE_BOT_RESPONSE,
  payload: {
    messages: flatMap(responses, response => {
      let prompts = response.prompts.map(prompt => ({
        message: prompt.content,
        owner: 'bot',
        type: 'message',
        timestamp: moment().format(),
      }));

      if (response.ui_component) {
        prompts = prompts.concat([
          {
            message: response.ui_component,
            owner: 'bot',
            type: 'ui_component',
            timestamp: moment().format(),
          },
        ]);
      }

      return prompts;
    }),
  },
});

/**
 * Bot is typing
 */
export const saveBotTyping = () => ({
  type: types.SAVE_BOT_TYPING,
});

/**
 * Start chat
 *
 * @returns {Function}
 */
export const startChat = () => async (dispatch, getState, ws) => {
  try {
    await dispatch({
      type: types.START_CHAT_REQUEST,
    });

    ws.send(
      JSON.stringify({
        type: wsStatuses['TYPE_DIALOG_START'],
        api_key: getToken(getState()),
        payload: {},
      }),
    );
  } catch (error) {
    await dispatch({
      type: types.START_CHAT_ERROR,
      payload: {
        error: error.message,
      },
    });
  }
};

export const startChatSuccess = (dialogId: string) => ({
  type: types.START_CHAT_SUCCESS,
  payload: { id: dialogId },
});

/**
 * Send text message
 *
 * @param utterance
 * @param context
 * @returns {Function}
 */
export const sendTextMessage = ({
  utterance = '',
  context = null,
}: {
  utterance?: string;
  context?: any;
}) => async (dispatch, getState, ws) => {
  try {
    const state = getState();
    const id = getChat(state).id;

    dispatch({
      type: types.SAVE_USER_RESPONSE,
      payload: {
        message: {
          owner: 'user',
          type: 'message',
          message: utterance,
          timestamp: moment().format(),
        },
      },
    });

    dispatch({
      type: types.SEND_TEXT_MESSAGE_REQUEST,
    });

    await sleep();

    await ws.send(
      JSON.stringify({
        type: wsStatuses['TYPE_DIALOG_MESSAGE'],
        api_key: getToken(state),
        payload: {
          dialog_id: id,
          utterance,
          context,
        },
      }),
    );

    dispatch({
      type: types.SEND_TEXT_MESSAGE_SUCCESS,
    });
  } catch (error) {
    dispatch({
      type: types.SEND_TEXT_MESSAGE_ERROR,
      payload: {
        error: error.message,
      },
    });
  }
};

/**
 * Open chatbox
 */
export const openChatBox = () => ({
  type: types.OPEN_CHAT_BOX,
});

/**
 * Toggle minimize chatbox
 */
export const toggleMinimizeChatBox = () => ({
  type: types.TOGGLE_MINIMIZE_CHAT_BOX,
});

/**
 * Close chatbox
 */
export const closeChatBox = () => (dispatch, getState, ws) => {
  const id = getChat(getState()).id;

  dispatch({
    type: types.CLOSE_CHAT_BOX,
  });

  ws.send(
    JSON.stringify({
      type: wsStatuses['TYPE_DIALOG_STOP'],
      api_key: getToken(getState()),
      payload: {
        dialog_id: id,
      },
    }),
  );
};

/**
 * Restart chatbox
 */
export const restartChat = () => async (dispatch, getState, ws) => {
  await dispatch({
    type: types.RESTART_CHAT,
  });

  //stop current session
  const id = getChat(getState()).id;

  await ws.send(
    JSON.stringify({
      type: wsStatuses['TYPE_DIALOG_STOP'],
      api_key: getToken(getState()),
      payload: {
        dialog_id: id,
      },
    }),
  );

  await startChat()(dispatch, getState, ws);
};

export function updateDialogId(id) {
  return {
    type: types.UPDATE_DIALOG_ID,
    payload: { id },
  };
}

/**
 * Set dialog status
 */
export const setDialogStatus = message => ({
  type: types.SET_DIALOG_STATUS,
  payload: {
    message: {
      message,
      owner: 'bot',
      type: 'status',
      timestamp: moment().format(),
    },
  },
});
