import * as actions from './actions';
import * as chatTypes from '../chat/actionTypes';

export type State = { message: string } | null;

// HACK: Dummy type to get the fix out.
type RestartAction = { type: string };

type Actions = actions.SetErrorAction | RestartAction;

function reducer(state: State = null, action: Actions): State {
  switch (action.type) {
    case actions.ERROR_MESSAGE: {
      const { payload } = action as actions.SetErrorAction;
      return { message: payload.message };
    }
    case chatTypes.RESTART_CHAT: {
      return null;
    }
  }
  return state;
}

export default reducer;
